<template>
  <div class="register">
    <!-- 导航 -->
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <!-- 内容 -->
    <div class="content">
      <div class="content-bg">
        <div class="content-box">
          <div class="content-list">
            <div class="title">{{ $t('register.title') }}</div>
            <div class="tabbar">
              <div
                class="tabbar-item"
                :class="[index === dlIndex ? 'active' : '']"
                v-for="(item, index) in dl"
                :key="index"
                @click="changeType(index)"
              >{{ item }}</div>
            </div>
            <div class="account">
              <el-form ref="loginForm" :model="form">
                <el-form-item prop="pass">
                  <el-row class="text">{{ $t('register.country') }}</el-row>
                  <el-row class="el-input-border" style="display: flex;">
                    <div class="el-input-choose" style="flex: 1;" @click="showCountry">
                      <div class="el-value">{{ form.country }}</div>
                      <i class="el-icon el-icon-caret-bottom"></i>
                    </div>
                    <div class="el-list-box" v-show="countryShow">
                      <div
                        class="el-list-box-item"
                        v-for="(item, index) in phoneList"
                        :key="index"
                        @click="choosePhone(index, 'country')"
                      >
                        <span class="country">{{ item.country }}</span>
                        <span class="pre">{{ item.zh }}</span>
                      </div>
                    </div>
                  </el-row>
                </el-form-item>
                <el-form-item prop="pass">
                  <el-row class="text">{{ $t('register.account') }}</el-row>
                  <el-row class="el-input-border" style="display: flex;">
                    <div v-show="dlIndex === 0" class="el-input-choose" @click="showPhone">
                      <div class="el-value">{{ form.prefix }}</div>
                      <i class="el-icon el-icon-caret-bottom"></i>
                    </div>
                    <el-input
                      class="el-input-row"
                      v-model="form.account"
                      type="text"
                      auto-complete="off"
                    ></el-input>
                    <div class="el-list-box el-list-box-2" v-show="countryTelShow">
                      <div
                        class="el-list-box-item"
                        v-for="(item, index) in phoneList"
                        :key="index"
                        @click="choosePhone(index, 'prefix')"
                      >
                        <span class="country">{{ item.country }}</span>
                        <span class="pre">{{ item.prefix }}</span>
                      </div>
                    </div>
                  </el-row>
                </el-form-item>
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('register.code_text') }}</el-row>
                  <el-row class="el-input-border" style="display: flex;">
                    <el-input
                      class="el-input-row"
                      v-model="form.code"
                      type="text"
                      auto-complete="off"
                    ></el-input>
                    <div class="el-input-choose el-input-choose-code">
                      <div class="el-value" @click="getCode">{{ btn_get_code }}</div>
                    </div>
                  </el-row>
                </el-form-item>
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('register.password_text') }}</el-row>
                  <el-input
                    class="el-input-row"
                    v-model="form.password"
                    type="password"
                    auto-complete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('register.password_again_text') }}</el-row>
                  <el-input
                    class="el-input-row"
                    v-model="form.password_again"
                    type="password"
                    auto-complete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('register.pid_text') }}</el-row>
                  <el-input
                    :disabled="pidDisabled"
                    class="el-input-row"
                    v-model="form.invitation_code"
                    type="text"
                    auto-complete="off"
                    :placeholder="$t('register.pid_pl')"
                  ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-checkbox v-model="checked" :label="$t('register.agree')" name="type"></el-checkbox>
                  <span class="xy" @click="path">&lt;&lt;{{ $t('register.xy') }}&gt;&gt;</span>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="register">{{ $t('register.register_btn') }}</el-button>
                </el-form-item>
              </el-form>
              <div class="bz">
                <p v-for="(item, index) in $t('register.bz')" :key="index">{{ item }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getTelCode, getCode, userRegister } from "@/api/index";
const pid_key = "jyxt_web_pid_20190709010101";
const inter = 60;

export default {
  components: {
    AoNav
  },
  data() {
    return {
      dlIndex: 0,
      dl: this.$t("register.dl"),
      form: {
        country: "",
        prefix: "",
        account: "",
        password: "",
        password_again: "",
        code: "",
        invitation_code: ""
      },
      countryShow: false,
      countryTelShow: false,
      phoneList: [],
      pidDisabled: false,
      btn_get_code: null,
      code_unit: "",
      r_code: 0,
      sendCodeFlag: false,
      checked: false
    };
  },
  mounted() {
    this.btn_get_code2 = this.$t("register.code_btn");
    this.btn_get_code = this.btn_get_code2;
    this.code_unit = this.$t("register.code_unit");

    var p = this.$route.query.p;
    var v = this.$route.query.v;
    if (!!v) {
      this.form.account = v;
      this.dlIndex = v.search("@") > -1 ? 1 : 0;
    }
    if (!p) {
      p = sessionStorage.getItem(pid_key);
    }
    this.form.invitation_code = "";
    if (p) {
      this.form.invitation_code = p;
      this.pidDisabled = true;
      sessionStorage.setItem(pid_key, p);
    }
    getTelCode().then(res => {
      this.form.country = res[0].country;
      this.form.prefix = res[0].prefix;
      this.phoneList = res;
    });
  },
  methods: {
    error(key) {
      this.$message({ message: this.$t("register." + key), type: "error" });
    },
    register() {
      console.log(this.form);
      if (!this.form.account) {
        this.error("account_no_data");
        return;
      }
      if (!this.form.code) {
        this.error("code_no_data");
        return;
      }
      if (this.r_code == 0 || this.r_code != this.form.code) {
        this.error("code_no_equality");
        return;
      }
      if (!this.form.password || !this.form.password_again) {
        this.error("password_no_data");
        return;
      }
      if (this.form.password != this.form.password_again) {
        this.error("password_no_equality");
        return;
      }
      if(!this.form.invitation_code){
        this.error("invitation_code_no_data");
        return;
      }
      if (!this.checked) {
        this.error("xy_no_checked");
        return;
      }
      this.form.index = this.dlIndex;
      this.form.type = 'register';
      userRegister(this.form)
        .then(res => {
          this.$router.push({
            path: "/login",
            query: {
              index: this.dlIndex,
              prefix: this.form.prefix,
              account: this.form.account,
              password: this.form.password
            }
          });
        })
        .catch(res => {
          console.log(res);
        });
    },
    getCode() {
      if (this.sendCodeFlag) return;
      if (!this.form.account) {
        this.$message({
          message: this.$t("register.code_no_data"),
          type: "error"
        });
        return;
      }
      this.sendCodeFlag = true;
      var data = {
        account: this.form.account,
        index: this.dlIndex,
        prefix: this.form.prefix,
        country: this.form.country
      };
      getCode(data)
        .then(({ code = 0 }) => {
          this.r_code = code;
          console.log(this.r_code);
          this.btn_get_code = inter + this.code_unit;
          var _inter = inter;
          var i = setInterval(() => {
            _inter--;
            this.btn_get_code = _inter + this.code_unit;
            if (_inter === 0) {
              clearInterval(i);
              this.btn_get_code = this.btn_get_code2;
              this.sendCodeFlag = false;
            }
          }, 1000);
        })
        .catch(() => {
          this.sendCodeFlag = false;
        });
    },
    showCountry() {
      this.countryShow = !this.countryShow;
      this.countryTelShow = false;
    },
    showPhone() {
      this.countryTelShow = !this.countryTelShow;
      this.countryShow = false;
    },
    choosePhone(index, key) {
      this.form[key] = this.phoneList[index][key];
      key === "prefix" ? this.showPhone() : this.showCountry();
    },
    changeType(index) {
      this.dlIndex = index;
      if (this.countryTelShow) this.countryTelShow = false;
    },
    path() {
      this.$router.push({
        path: "/text",
        query: { id: 3 }
      });
    }
  }
};
</script>

<style lang="scss">
.register {
  .content {
    background-color: #1a243b;
  }
  .title {
    font-size: 30px;
    color: #c7cce6;
  }
  .content-bg {
    background: url(/static/image/bg_login.png) center bottom 316px repeat-x,
      -webkit-linear-gradient(top, #21263f, #171c2b);
  }
  .content-box {
    width: 1200px;
    // margin: 0 auto;
    padding-top: 93px;
    overflow: hidden;
    min-height: 880px;
  }
  .content-list {
    position: relative;
    padding: 0 0 60px 30px;
    .tabbar {
      display: flex;
      margin-left: 1rem;
      margin-top: 0.5rem;
      .tabbar-item {
        padding-bottom: 5px;
        margin-right: 40px;
        cursor: pointer;
        color: #c7cce6;
        &.active {
          color: #7a98f7;
          border-bottom: 2px solid #7a98f7;
        }
      }
    }
    .account {
      display: flex;
    }
    .account .el-form {
      width: 520px;
      .text {
        display: block;
        height: 22px;
        color: #61688a;
        font-size: 12px;
      }
      .xy {
        color: #7a98f7;
        margin-left: 5px;
        font-size: 12px;
        cursor: pointer;
      }
      .el-checkbox {
        .el-checkbox__inner {
          background-color: #1e2235;
          border-color: #c7cce6;
        }
        .el-checkbox__label {
          color: #c7cce6;
        }
      }
      .xy {
      }
      .el-form-item {
        margin-bottom: 8px;
      }
      .el-input-border {
        border: 1px solid #4e5b85;
        border-radius: 3px;
        position: relative;
        margin-top: 8px;
        .el-input-row {
          margin-top: 0;
          border: none;
        }
        .el-list-box {
          width: 100%;
          position: absolute;
          z-index: 5;
          left: 0;
          right: 0;
          top: 43px;
          height: 250px;
          overflow-y: auto;
          box-shadow: 0 2px 6px 0 #000;
          border-color: #d1d3df;
          background-color: #171c2b;
          color: #9194a4;
        }
        .el-list-box-2 {
          top: 48px;
        }
        .el-list-box-item {
          padding: 15px;
          border-bottom: 1px solid #303b4b;
          display: flex;
          justify-content: space-between;
          align-items: center;
          cursor: pointer;
        }
      }
      .el-input-choose {
        display: flex;
        width: 146px;
        align-items: center;
        background-color: #1e2235;
        border-right: 1px solid #4e5b85;
        cursor: pointer;
        &.el-input-choose-code {
          border-left: 1px solid #4e5b85;
          border-right: none;
          text-align: center;
        }
        .el-value {
          flex: 1;
          text-indent: 0.5rem;
        }
        .el-value,
        .el-icon {
          width: 2.6rem;
          color: #c7cce6;
          font-size: 14px;
        }
      }
      .el-input-row {
        width: 520px;
        min-height: 46px;
        border: 1px solid #4e5b85;
        color: #c7cce6;
        font-size: 14px;
        border-radius: 3px;
        background-color: #1e2235;
        position: relative;
        margin-top: 8px;
      }
      .el-input__inner {
        border: none;
        height: 44px;
        padding-left: 10px;
        padding-right: 10px;
        background: transparent;
        color: #c7cce6;
      }
      .el-button {
        flex: 1;
        width: 200px;
        display: inline-block;
        line-height: 46px;
        background-color: #7a98f7;
        border-radius: 4px;
        color: #fff;
        border: none;
        padding: 0;
      }
      .forgetpassword {
        font-size: 14px;
        color: #7a98f7;
        cursor: pointer;
        margin-left: 1rem;
      }
    }
    .account .bz {
      margin-left: 5rem;
      margin-top: 2rem;
      font-size: 14px;
      p {
        font-size: 14px;
        color: #61688a;
        line-height: 24px;
        &.forgetpassword {
          font-size: 14px;
          color: #7a98f7;
          cursor: pointer;
          margin-top: 1rem;
        }
      }
    }
  }
}
</style>



